import { MutableRefObject, useEffect, useMemo, useRef, useState } from 'react'
import { PostListItemType } from 'types/PostItem.types'

// Type
export type useInfiniteScrollType = {
  containerRef: MutableRefObject<HTMLDivElement | null>
  postList: PostListItemType[]
}

// ÌéòÏù¥ÏßÄÎãπ Î∂àÎü¨Ïò¨ ÏïÑÏù¥ÌÖú Í∞ØÏàò
const NUMBER_OF_ITEMS_PER_PAGE = 10

// Î¨¥Ìïú Ïä§ÌÅ¨Î°§
const useInfiniteScroll = function (
  selectedCategory: string,
  posts: PostListItemType[],
): useInfiniteScrollType {
  // Ïä§ÌÅ¨Î°§ Ïª®ÌÖåÏù¥ÎÑà ref
  const containerRef: MutableRefObject<HTMLDivElement | null> =
    useRef<HTMLDivElement>(null)

  // Ïä§ÌÅ¨Î°§ Ïª®ÌÖåÏù¥ÎÑà Í¥ÄÏ∞∞Ïûê
  const observer: MutableRefObject<IntersectionObserver | null> =
    useRef<IntersectionObserver>(null)

  // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Ïàò
  const [count, setCount] = useState<number>(1)

  // Ïπ¥ÌÖåÍ≥†Î¶¨Î≥Ñ Ìè¨Ïä§Ìä∏ ÌïÑÌÑ∞ÎßÅ
  const postListByCategory = useMemo<PostListItemType[]>(
    () =>
      posts.filter(
        ({
          node: {
            frontmatter: { categories },
          },
        }: PostListItemType) =>
          selectedCategory !== 'All'
            ? categories.includes(selectedCategory)
            : true,
      ),
    [selectedCategory],
  )

  // Ïä§ÌÅ¨Î°§ Ïª®ÌÖåÏù¥ÎÑà Í¥ÄÏ∞∞Ïûê ÏÑ§Ï†ï
  useEffect(() => {
    observer.current = new IntersectionObserver((entries, observer) => {
      if (!entries[0].isIntersecting) return

      setCount(value => value + 1)
      observer.unobserve(entries[0].target)
    })
  }, [])

  // Ïπ¥ÌÖåÍ≥†Î¶¨ Î≥ÄÍ≤Ω Ïãú ÌéòÏù¥ÏßÄ Ïàò Ï¥àÍ∏∞Ìôî
  useEffect(() => setCount(1), [selectedCategory])

  // Ïä§ÌÅ¨Î°§ Ïª®ÌÖåÏù¥ÎÑà Í¥ÄÏ∞∞Ïûê ÏÑ§Ï†ï
  useEffect(() => {
    if (
      NUMBER_OF_ITEMS_PER_PAGE * count >= postListByCategory.length ||
      containerRef.current === null ||
      containerRef.current.children.length === 0 ||
      observer.current === null
    )
      return

    observer.current.observe(
      containerRef.current.children[containerRef.current.children.length - 1],
    )
  }, [count, selectedCategory])

  return {
    containerRef,
    postList: postListByCategory.slice(0, count * NUMBER_OF_ITEMS_PER_PAGE),
  }
}

export default useInfiniteScroll

/*
Ïù¥ ÏΩîÎìúÎäî Î¨¥Ìïú Ïä§ÌÅ¨Î°§ Í∏∞Îä•ÏùÑ Íµ¨ÌòÑÌïòÎäî Ïª§Ïä§ÌÖÄ ÌõÖÏûÖÎãàÎã§. Ï£ºÏöî Í∏∞Îä•ÏùÄ Îã§ÏùåÍ≥º Í∞ôÏäµÎãàÎã§:

1. Ìè¨Ïä§Ìä∏ ÌïÑÌÑ∞ÎßÅ
- selectedCategoryÏóê Îî∞Îùº Ìè¨Ïä§Ìä∏Î•º ÌïÑÌÑ∞ÎßÅÌï©ÎãàÎã§
- 'All' Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä ÏÑ†ÌÉùÎêú Í≤ΩÏö∞ Î™®Îì† Ìè¨Ïä§Ìä∏Î•º ÌëúÏãú
- ÌäπÏ†ï Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä ÏÑ†ÌÉùÎêú Í≤ΩÏö∞ Ìï¥Îãπ Ïπ¥ÌÖåÍ≥†Î¶¨Ïùò Ìè¨Ïä§Ìä∏Îßå ÌïÑÌÑ∞ÎßÅ

2. Intersection Observer ÏÑ§Ï†ï
- Ïä§ÌÅ¨Î°§ Ïª®ÌÖåÏù¥ÎÑàÏùò ÎßàÏßÄÎßâ ÏöîÏÜåÎ•º Í¥ÄÏ∞∞
- ÎßàÏßÄÎßâ ÏöîÏÜåÍ∞Ä ÌôîÎ©¥Ïóê Î≥¥Ïù¥Î©¥(isIntersecting) countÎ•º 1 Ï¶ùÍ∞ÄÏãúÌÇ¥
- count Ï¶ùÍ∞ÄÎ°ú Îçî ÎßéÏùÄ Ìè¨Ïä§Ìä∏Í∞Ä Î°úÎìúÎê®

3. Ïπ¥ÌÖåÍ≥†Î¶¨ Î≥ÄÍ≤Ω Ï≤òÎ¶¨
- Ïπ¥ÌÖåÍ≥†Î¶¨Í∞Ä Î≥ÄÍ≤ΩÎê† ÎïåÎßàÎã§ countÎ•º 1Î°ú Ï¥àÍ∏∞Ìôî
- ÏÉàÎ°úÏö¥ Ïπ¥ÌÖåÍ≥†Î¶¨Ïùò Ï≤´ ÌéòÏù¥ÏßÄÎ∂ÄÌÑ∞ ÌëúÏãú

4. Ïä§ÌÅ¨Î°§ Í∞êÏßÄ Î∞è Ìè¨Ïä§Ìä∏ Î°úÎìú
- NUMBER_OF_ITEMS_PER_PAGE * countÎ°ú ÌëúÏãúÌï† Ìè¨Ïä§Ìä∏ Ïàò Í≥ÑÏÇ∞
- Ïª®ÌÖåÏù¥ÎÑàÏùò ÎßàÏßÄÎßâ ÏöîÏÜåÎ•º Í¥ÄÏ∞∞ÌïòÏó¨ Ï∂îÍ∞Ä Î°úÎìú ÏãúÏ†ê Í≤∞Ï†ï
- Î™®Îì† Ìè¨Ïä§Ìä∏Í∞Ä Î°úÎìúÎêòÎ©¥ Ï∂îÍ∞Ä Í¥ÄÏ∞∞ Ï§ëÏßÄ

5. Î∞òÌôòÍ∞í
- containerRef: Ïä§ÌÅ¨Î°§ Ïª®ÌÖåÏù¥ÎÑà Ï∞∏Ï°∞
- postList: ÌòÑÏû¨ ÌëúÏãúÌï† Ìè¨Ïä§Ìä∏ Î™©Î°ù
*/
